<template>
    <div>
        <headers :title="title"></headers>
        <div class="set-write">
            <ul>
                <li>
                    <font class="fl">修改卡密</font><i class="fr"></i>
                </li>
                 <li>
                    <font class="fl">支付密码</font><span class="fl">(未设置)</span><i class="fr"></i>
                </li>
            </ul>
        </div>
    </div>
</template>

<style lang="less" scoped>
    .set-write{
        font-family: "PingFang-SC-Medium";
        margin-top: 180px;
        background: #fff;
        border-top: 1px solid #D4D4D6;
        width: 750px;
        ul{
            padding-left: 32px;
            li{         
                font-size: 28px;
                color: #000;   
                height: 98px;     
                border-bottom: 1px solid #D4D4D6;
                font{
                    line-height: 98px;
                }         
                i{
                    width: 16px;
                    height: 98px;
                    background: url("../../assets/imgs/add-go.png") no-repeat left center;
                    background-size: 100%;
                    margin-right: 33px;
                }
                span{
                    padding-left: 20px;
                    line-height: 98px;
                }
            }
        }
       
    }
</style>

<script>
import "lib-flexible"
import headers from '../../components/editHeader.vue'
export default {
  components:{
      headers
  },
  data(){
      return{
          title: "密码管理"
      }
  },
  mounted(){
      document.body.style.backgroundColor = "rgb(247,246,247)";
  }
}
</script>





